
<template>
	<div>
		<default-header/>
		<div class="ui container margin-before-lg">
			<table class="ui very basic selectable table">
			  <thead>
			    <tr>
			    	<th class="two wide">Last Visit</th>
			    	<th class="five wide">Name</th>
			    	<th class="five wide">Date of Birth</th>
			  	</tr>
				</thead>
			  <tbody v-if="patientListLoadStatus==2">
			    <tr v-for="patient in patientList" 
			    		@click="displayPatientDetail(patient.id)"
			    		class="pointer tall-row">
			      <td>{{ patient.last_visit | formatBirthdate }}</td>
			      <td><h4>{{ patient.first_name }} {{ patient.last_name }}</h4></td>
			      <td>{{ patient.dob | formatBirthdate }}</td>
			    </tr>
			  </tbody>
			</table>

			<div v-if="patientListLoadStatus==0">
				Loading...
			</div>	
		</div>
	</div>
</template>

<script> 

import Header from './Header.vue'

export default {
	created() {
		this.$store.dispatch('loadPatientList');
	},
	components: {
		"default-header": Header
	},
	computed: {
		patientListLoadStatus() {
			return this.$store.getters.getPatientListLoadStatus
		},
		patientList() {
			return this.$store.getters.getPatientList
		}
	},
	methods: {
		displayPatientDetail(id) {
			this.$router.push({ 
        name: 'Patient Profile', 
        params: {
          id: `${id}` 
        } 
      })
		}
	},
	filters: {
		formatBirthdate( value ){
			if (!value) return ''
			return value.format("MMM DD, YYYY")
		},
	}
}
</script>

<style scoped>

.font-size-lg {
	font-size: 2rem;
}
.margin-before-lg {
	margin-top: 8rem;
}
.margin-before-sm {
	margin-top: 2rem;
}
.margin-before-md {
	margin-top: 4rem;
}
.pointer {
	cursor: pointer
}
.tall-row {}
.tall-row > td {
	padding-top: 1.4rem !important;
	padding-bottom: 1.4rem !important;
}

</style>


