
<template>
	<div :class="{ 'ui':true, 'wide':true, 'bottom':true, 'card':true, 'slideDown': (collapsed != 'expanded') }">	
		<div class="content" @click="expandForm">
	    <button v-show="collapsed==='expanded'"
	    				class="ui compact icon button right floated"
	    				@click="minimizeForm">
      	<i class="minus icon"></i>
      </button>
      <button v-show="collapsed!=='expanded'"
      				class="ui compact icon button right floated">
      	<i class="plus icon"></i>
      </button>
      <div class="header">{{ patientName }}</div>
      <div class="meta"> Today {{ saveTime | formatTime }}</div>
			<div v-show="collapsed==='expanded'" class="ui divider long"></div>
	  
	    <div ref="followupForm" 
	    		 v-show="collapsed==='expanded'"
	    		 class="description scrollable">
	      <formatted-input labelCopy="Results From Last Treatment" v-model="form.results_from_last_treatment"/>
				<!-- <div class="ui divider"></div> -->
	      <formatted-input labelCopy="Changes In Symptom" v-model="form.changes_in_symptom"/>
				<!-- <div class="ui divider"></div> -->
	      <formatted-input labelCopy="Symptom Differentiation" v-model="form.symptom_differentiation"/>
				<!-- <div class="ui divider"></div> -->
	      <formatted-input labelCopy="Treatment Principle" v-model="form.treatment_principle"/>
				<!-- <div class="ui divider"></div> -->
	      <formatted-input labelCopy="Treatment" v-model="form.treatment"/>
	    </div>
    </div>
    
    <div v-show=" collapsed==='expanded' " class="extra content">
      <div class="ui primary large button right floated" @click="saveForm">Save Entry</div>
      <div class="ui basic link large button right floated" @click="discardForm">Discard</div>
    </div>

	</div>

</template>

<script> 
import FormattedInput from './BaseFormattedInput.vue'
import moment from 'moment'

export default {
	props: ['patientName'],
	components: {
		'formatted-input': FormattedInput
	},
	filters:{
		formatTime( value ) {
			if (!value) return ""
			return ` • Last saved ${moment(value).format("hh:MM A")}`
		}
	},
	data() {
		return {
			saveTime: "",
			collapsed: "expanded",
			form: {
				results_from_last_treatment: "",
				changes_in_symptom: "",
				symptom_differentiation: "",
				treatment_principle: "",
				treatment: "",
			}
		}
	},
	methods: {
		saveForm() {
			this.saveTime = moment.now()
			this.$store.dispatch('createFollowup', this.form)
			this.$emit('submit')
		},
		discardForm(){
			for (var key in this.form) {
				this.form[key] = ''
			}
			this.$emit('hide')
		},
		minimizeForm(){
			this.collapsed = "transitioning"
			setTimeout( ()=>{
				this.collapsed = "collapsed"
			}, 300)
			
		},
		expandForm(){
			if (this.collapsed === "collapsed")
				this.collapsed = "expanded"
		}
	}
}
</script>

