	max-height: 80vh;
	padding: 1rem 0.5rem 0rem 0.5rem;
	box-shadow: 0px 0px 2px 1px rgba(0,0,0,0.3);
	border: 1px solid #fff !important;
}
.scrollable {
	margin-left: 0;
	margin-right: 0;
	padding-top: 2rem;
	max-height: 400px;
	overflow-y: scroll;
}
